export const markedInline = (md) => marked.inlineLexer(md, []);

export const urlForStatic = (path) => {
  if (path[0] === '/') {
    path = path.slice(1);
  }
  return `/static/${path}`;
}

export const requireImage = require.context('../../content', true, /\.(png|jpe?g|gif)$/);

export const requireImageSmall = require.context('../../content?resize&size=400', true, /\.(png|jpe?g)$/);
